<template>
    <div v-if="ready" style='height:87.5vh'>
        <div class="col-12">
            <div id='top_page' class="form-group">       
                <div class='col-12 flex px0'>
                    <div class="col-4 px0">
                        <label>Search Record</label>
                        <input type='text' class="form-control" placeholder="Search Record" v-model="key" @keyup="fetch_info()">
                    </div>
                    <div class="col-5"></div>
                    <div class="col-3 px0">         
                        <label>School Year</label>                
                        <div class="col-12 flex px0">
                            <div class="col-6 flex pl0">
                                <input type="number" v-model="st" class="form-control" @change="update_period()"/>
                            </div>
                            <div class="col-6 flex pr0">
                                <input type="number" v-model="ed" class="form-control" readonly/>
                            </div>
                        </div>   
                    </div>
                </div>
            </div>  
            <table class="table table-striped first-td-padding">
                <thead style="background-color:rgb(238,28,37)">
                    <tr>
                        <th style='vertical-align:top'>Action</th>
                        <th style='width:5%!important;vertical-align:top'>ID</th>
                        <th style='vertical-align:top'>Date</th>
                        <th style='vertical-align:top'>Procurement ID</th>
                        <th style='vertical-align:top'>Department</th>
                        <th style='vertical-align:top'>Recipient</th>
                        <th style='vertical-align:top'>Item Description</th>
                        <th style='vertical-align:top'>Fund Source</th>
                        <th style='vertical-align:top'>Amount HK$</th>
                        <th style='vertical-align:top'>Status</th>
                        <th style='vertical-align:top'>Form</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="l in data.length" style='font-size:1rem!important'>
                        <td style='font-size:1rem!important'>
                            <u v-if="(data[l-1].approve_status=='pending')&&(data[l-1].system_stat=='enable')&&(data[l-1].dept!='Med')" @click="edit_reimb_record(data[l-1].tag)">Edit</u>
                        </td>
                        <td style='font-size:1rem!important'>{{ data[l-1].tag.toUpperCase() }}</td>
                        <td style='font-size:1rem!important'>{{ data[l-1].date_app }}</td>
                        <td style='font-size:1rem!important'>{{ data[l-1].proc_id }}</td>
                        <td style='font-size:1rem!important'>{{ data[l-1].dept_full }}</td>
                        <td style='font-size:1rem!important'>{{ data[l-1].receiver_name }}</td>
                        <td style='font-size:1rem!important'>
                            <div class='inner_row' v-for="item in data[l-1].item.length">
                                {{ data[l-1].item[item-1].nature }}
                            </div>
                        </td>
                        <td style='font-size:1rem!important'>
                            <div class='inner_row' v-for="item in data[l-1].item.length">
                                {{ data[l-1].item[item-1].fundsrc }}
                            </div>
                        </td>
                        <td style='font-size:1rem!important'>
                            <div class='inner_row' v-for="item in data[l-1].item.length">
                                {{ data[l-1].item[item-1].amount }}
                            </div>
                        </td>
                        <td style='font-size:1rem!important'>{{ data[l-1].approve_status }}</td>
                        <td style='font-size:1rem!important'>
                            <a v-if="data[l-1].system_stat=='enable'" @click="view_form(data[l-1].tag)"><u>View</u></a>
                            <a v-if="data[l-1].system_stat=='req'">To Be Processed</a>
                        </td>
                    </tr>
                </tbody>
            </table>       
        </div>
        <div class="corner_nav">
            <div class="col-12">
                <div class="corner_item" @click="scrollTop">
                    <i class="fa fa-2x fa-arrow-up" style='color:rgba(255,255,255,0.9)'></i>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            key:'',
            st:'',
            ed:'',
            data:[],            
            ready:false,
        }
    },
    async created(){
        await axios.get('Procurement/shared_backend/check_user_setup.php')
        .then(
            (response)=>{
                var user_check='';
                user_check = response.data[0].signed;
                if(!(user_check=='set')){
                    this.$router.push('../user_setup');
                }
            }
        )
        await axios.get('Procurement/shared_backend/default_period.php')
            .then(
                (response)=>{
                    var st = response.data.st.split("-");
                    var ed = response.data.ed.split("-");
                    this.st = st[0];
                    this.ed = parseInt(ed[0]);
                }
            )
        await this.fetch_info();
        this.ready = true;
    },
    methods:{
        edit_reimb_record(tag){
            document.getElementById('tmp_id').value = tag;
            document.getElementById('st_tmp').value=this.st+'-08';
            document.getElementById('ed_tmp').value=this.ed+'-07';
            this.$router.push('edit_reimb_record');
        },
        scrollTop(){
            document.getElementById('top_page').scrollIntoView();
        },
        async update_period(){
            this.ed = parseInt(this.st)+1;
            this.$forceUpdate();
            await this.fetch_info();
        },
        view_form(tag){
            document.getElementById('tmp_id').value = tag;
            document.getElementById('st_tmp').value = this.st+'-08';
            document.getElementById('ed_tmp').value = this.ed+'-07';
            document.getElementById('doc_type').value = 'Reimbursement';
            this.$router.push('../wrapper/form_view');
        },
        async fetch_info(){
            var formData = new FormData();
            formData.append('key',this.key);
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            await axios.post('Procurement/reimbursement/backend/fetch_my_reimb.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.data = response.data;
                        } else {
                            this.data = [];
                        }
                    }
                )
        }
    }
}
</script>
<style scoped>
    .flex{
        display:flex;
        flex-direction: row;
    }
    .px0{
        padding-left:0;
        padding-right: 0;
    }
    .pl0{
        padding-left:0;
    }
    .pr0{
        padding-right:0;
    }
    th,td{
        font-size:14px!important;   
        border:1px solid white;
    }
    td{
        font-size:1rem!important;
    }
    td,th{
        vertical-align:top;
    }
    .inner_row{
        height:3rem;
    }
    .approved{
        color:#1bd615;
        text-transform: capitalize;
        font-size:1rem;
    }
    .pending{
        text-transform: capitalize;
        font-size:1rem;
    }
    .inner_row{
        height:3rem;
    }
    .cancelled, .rejected{
        color:red;
        text-transform: capitalize;
        font-size:1rem;
    }
    u,a{
        cursor:pointer;
    }
</style>


